<template>
  <div class="channel-edit">
    <van-cell title="我的频道">
      <van-button
        type="danger"
        size="small"
        round
        plain
        @click="isEdit = !isEdit"
        >{{ isEdit ? "完成" : "编辑" }}</van-button
      >
    </van-cell>
    <van-grid :gutter="6">
      <van-grid-item
        v-for="(value, index) in channels"
        :key="index"
        :text="value.name"
        :class="{ active: index === active }"
        @click="handleClickMyChannel(index)"
      >
        <van-icon name="close" slot="icon" v-if="isEdit"></van-icon>
      </van-grid-item>
    </van-grid>
    <van-cell title="我的推荐"> </van-cell>
    <van-grid :gutter="6">
      <van-grid-item
        @click="handleAddMyChannel(value)"
        v-for="value in recommendChannel"
        :key="value.id"
        icon="plus"
        :text="value.name"
      ></van-grid-item>
    </van-grid>
  </div>
</template>

<script>
import { getAllChannels, updateChannels } from '@/api/channels'

export default {
  name: 'channels',
  props: {
    channels: {
      type: Array,
      required: true
    },
    active: {
      type: Number,
      required: true
    }
  },
  data () {
    return {
      // 所有频道
      allChannels: [],
      isEdit: false // 编辑状态
    }
  },
  computed: {
    // 直接当做普通属性调用不加括号
    // 任何data中数据变化立即重新计算
    // 计算属性会缓存
    recommendChannel () {
      // 从所有频道里面筛选出没有在我的频道里面的数据
      return this.allChannels.filter(t => {
        const f = this.channels.find(m => m.id === t.id)
        // 如果我到了，f不会null,如果没找到f为nll
        // return f?false: true
        return !f
      })
    }
  },
  methods: {
    async handleAddMyChannel (item) {
      // 方式1-通过子传父,通过父组件
      // this.$emit('add', item)
      // 方式2-直接操作父组件传过数据
      this.channels.push(item)
      // 根据有没有token来判断
      if (this.$store.state.token) {
        // 如果登录,发请求
        await updateChannels({ channels: this.channels })
      // console.log(this.active)
      // this.active=1
      } else {
        // 如果没有登录,存储本地
        localStorage.toutiao_channels = JSON.stringify(this.channels)
      }
    },
    async handleClickMyChannel (index) {
      if (this.isEdit) {
        // 编辑模式,删除
        if (index <= this.active) {
          this.$emit('update-active', this.active - 1, true)
        }
        this.channels.splice(index, 1)
        // if (this.$store.state.token) {
        // // 如果登录,发请求
        //   await updateChannels({ channels: this.channels })
        //   // console.log(this.active)
        //   // this.active=1
        // } else {
        // // 如果没有登录,存储本地
        //   localStorage.toutiao_channels = JSON.stringify(this.channels)
        // }
      } else {
        // 切换
        this.$emit('update-active', index)
      }
    }
  },
  watch: {
    async channels (val) {
      if (this.$store.state.token) {
        // 如果登录,发请求
        await updateChannels({ channels: this.channels })
        // console.log(this.active)
        // this.active = 1
      } else {
        // 如果没有登录,存储本地
        localStorage.toutiao_channels = JSON.stringify(this.channels)
      }
    }
  },
  async created () {
    const res = await getAllChannels()
    this.allChannels = res.data.data.channels
  }
}
</script>

<style lang="less" scoped>
.channel-edit {
  padding-top: 80px;
}
.van-cell__title {
  font-size: 32px;
  color: #333;
}
.van-button--danger {
  width: 104px;
  height: 48px;
}
/deep/ .active {
  .van-grid-item__text {
    color: red !important;
  }
}
/deep/ .van-grid-item__content {
  .van-icon-plus {
    font-size: 28px;
    margin-top: 6px;
  }
  .van-grid-item__icon-wrapper {
    position: unset;
  }
  .van-icon-close {
    position: absolute;
    top: -12px;
    right: -12px;
    font-size: 32px;
  }
  .van-grid-item__text {
    color: #222;
    margin-top: 0 !important;
  }
  background-color: #f5f5f6;
  display: flex;
  flex-direction: row;
}
</style>
