import request from '../utils/request.js'
// import store from '@/store/index.js'
export function getUserChannels (data) {
  return request({
    url: 'v1_0/user/channels',
    method: 'get',
    data
  })
}

export function getAllChannels () {
  return request({
    url: 'v1_0/channels',
    method: 'get'
  })
}

export function updateChannels (data) {
  return request({
    url: 'v1_0/user/channels',
    method: 'put',
    data
  })
}
