<template>
    <div class="artile-list" ref="listRef">
        <van-pull-refresh v-model="refreshing" :success-duration="2000" @refresh="onRefresh">
            <van-list v-model="loading" :finished="finished" finished-text="没有更多了" :error.sync="error"
                error-text="请求失败,点击重新加载" @load="onLoad">
                <!-- <van-cell v-for="item in list" :key="item.id" :title="item.title" /> -->
            <article-item v-for="item in list" :key="item.id" :article="item"></article-item>
            </van-list>
        </van-pull-refresh>
    </div>
</template>

<script>
import { getArticleList } from '@/api/article.js'
import articleItem from '@/components/article-item.vue'

export default {
  components: { articleItem },
  name: 'articleList',
  props: {
    channel: {
      type: Object,
      required: true
    }
  },
  data () {
    return {
      list: [], // 存储列表数据的数组
      loading: false, // 控制加载中 loading 状态
      finished: false, // 控制数据加载结束的状态
      error: false, // 是否错误
      timestamp: Date.now(),
      refreshing: false, // 是否下拉刷新中
      refreshTip: ''
    }
  },

  activated () {
    console.log('activated')
    // 回来
    this.$refs.listRef.scrollTop = localStorage.scrollTop || 0
  },
  deactivated () {
    console.log('deactivated')
    // 离开
  },
  created () {},
  mounted () {
    this.$refs.listRef.onscroll = function () {
      localStorage.scrollTop = this.scrollTop
    }
  },
  methods: {
    // 页面打开&触底执行
    async onLoad () {
      try {
        // 1发送请求
        const { data: { data: { results, pre_timestamp: timestamp } } } = await getArticleList({
          channel_id: this.channel.id,
          timestamp: this.timestamp
        })
        //   console.log(res)
        //   const { data: { data: { results, pre_timestamp: timestamp } } } = res
        // 模拟错误
        // if (Math.random() > 0.5) {
        //   JSON.parse('{username: "旺旺"}')
        // }
        // 2.追加数据
        this.list.push(...results)
        // 更新时间戳
        this.timestamp = timestamp
        // 3.加载状态结束
        this.loading = false
        //   this.loading = true
        // 4.判断数据全部加载完成
        if (timestamp == null || results.length === 0) {
          this.finished = true
        }
      } catch (e) {
        this.loading = false
        this.error = true
      }
    },

    async onRefresh () {
      try {
        // 1.发送请求
        const { data: { data: { results } } } = await
        getArticleList({
          channel_id: this.channel.id,
          timestamp: Date.now()
        })
        // 模拟错误
        // JSON.parse('{username: "旺旺"}')
        // 2.放置数据
        this.list.unshift(...results)
        // 3.关闭状态值
        this.refreshing = false
        this.refreshTip = '获取数据成功'
      } catch (error) {
        this.refreshing = false
        this.refreshTip = '获取数据失败,请稍后重试'
      }
    }
  }

}
</script>

<style lang="less" scoped>
.artile-list{
    height: 79vh;
    overflow-y: auto;
}
</style>
